@import '../../appScss/typography';
@import '../../appScss/colors';
@import '../../appScss/mixins';

.add_to_card {
  width: 100%;
  height: 21px;
  padding-block: 10px;
  background: var(--btn);
  color: var(--white-color);
  border: 1px solid var(--btn);
  box-sizing: content-box;
  transition: all 0.3s;
  z-index: 1;
  cursor: pointer;
  @include btns;

  &:hover {
    box-shadow: 0px 3px 13px 0px (rgba(23, 32, 49, 0.4));
  }

  &__added {
    border: 1px solid var(--elements-color);
    color: var(--added-color);
    background: var(--additional-btn);

    @include onPhone {
      font-size: 11px;
    }
  }
}

[theme='dark'] {
  .add_to_card {
    &:hover {
      background-color: var(--btn-hover);
    }

    &__added {
      border: 1px solid var(--additional-btn);

      &:hover {
        background-color: var(--additional-btn);
        border: 1px solid var(--additional-btn-hover);
      }
    }
  }
}
